body {
  font-family: Helvetica, Arial, sans-serif;
  padding: 40px;
}
input {
  font-size: 1.3rem;
  width: 100%;
  color: #333;
}
.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p {
  padding: 5px;
  text-align: center;
}
.card img {
  max-width: 170px;
}
